.projects_layout{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: start;
}

.project_item{
    width: 380px;
    transition: width 100ms linear;
}

@media screen and (max-width: 1400px) {
    .project_item{
        width: 45%;
        transition: width 100ms linear;
    }
}

@media screen and (max-width: 768px) {
    .project_item{
        width: 400px;
        transition: width 100ms linear;
    }
}